/* 头像旋转 */
.home .home-profile .home-avatar img {
  width: 9rem;

/* 设置循环动画
[animation: 
(play)动画名称
(2s)动画播放时长单位秒或微秒
(ease-out)动画播放的速度曲线为以低速结束 
(1s)等待1秒然后开始动画
(1)动画播放次数(infinite为循环播放) ]*/

/* 鼠标经过头像旋转360度 */
-webkit-transition: -webkit-transform 1.0s ease-out;
-moz-transition: -moz-transform 1.0s ease-out;
transition: transform 1.0s ease-out;
  &:hover {
    /* 鼠标经过停止头像旋转 
    -webkit-animation-play-state:paused;
    animation-play-state:paused;*/

    /* 鼠标经过头像旋转360度 */
    -webkit-transform: rotateZ(360deg);
    -moz-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}
/* Z 轴旋转动画 */
@-webkit-keyframes play {
0% {
  -webkit-transform: rotateZ(0deg);
}
100% {
  -webkit-transform: rotateZ(-360deg);
}
}
@-moz-keyframes play {
0% {
  -moz-transform: rotateZ(0deg);
}
100% {
  -moz-transform: rotateZ(-360deg);
}
}
@keyframes play {
0% {
  transform: rotateZ(0deg);
}
100% {
  transform: rotateZ(-360deg);
}
}



/* website-1 样式 -------------------- 开始 -------------------- */
:root {
  --global-font-size: 15px;
  --global-bg: #fff;
  --font-color: #4c4948;
  --hr-border: #a4d8fa;
  --hr-before-color: #80c8f8;
  --search-bg: #f6f8fa;
  --search-input-color: #4c4948;
  --search-a-color: #4c4948;
  --preloader-bg: #37474f;
  --preloader-color: #fff;
  --tab-border-color: #f0f0f0;
  --tab-botton-bg: #f0f0f0;
  --tab-botton-color: #1f2d3d;
  --tab-button-hover-bg: #dcdcdc;
  --tab-button-active-bg: #fff;
  --card-bg: #fff;
  --sidebar-bg: #f6f8fa;
  --btn-hover-color: #ff7242;
  --btn-color: #fff;
  --btn-bg: #49b1f5;
  --text-bg-hover: rgba(73,177,245,0.7);
  --light-grey: #eee;
  --dark-grey: #cacaca;
  --white: #fff;
  --text-highlight-color: #1f2d3d;
  --blockquote-color: #6a737d;
  --blockquote-bg: rgba(73,177,245,0.1);
  --reward-pop: #f5f5f5;
  --toc-link-color: #666261;
  --card-box-shadow: 0 3px 8px 6px rgba(7,17,27,0.05);
  --card-hover-box-shadow: 0 3px 8px 6px rgba(7,17,27,0.09);
  --pseudo-hover: #ff7242;
  --headline-presudo: #a0a0a0;
  --scrollbar-color: #49b1f5;
  --default-bg-color: #49b1f5
}

#website-container {
  word-wrap: break-word;
  overflow-wrap: break-word
}

#website-container a {
  color: #49b1f5
}

#website-container a:hover {
  text-decoration: underline;
  background-color: #49b1f5;
}

#website-container img {
  display: block;
  margin: 0 auto 20px;
  max-width: 100%;
  transition: filter 375ms ease-in .2s
}

#website-container p {
  margin: 0 0 16px
}

#website-container iframe {
  margin: 0 0 20px
}

#website-container kbd {
  margin: 0 3px;
  padding: 3px 5px;
  border: 1px solid #b4b4b4;
  border-radius: 3px;
  background-color: #f8f8f8;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .25), 0 2px 1px 0 rgba(255, 255, 255, .6) inset;
  color: #34495e;
  white-space: nowrap;
  font-weight: 600;
  font-size: .9em;
  font-family: Monaco, 'Ubuntu Mono', monospace;
  line-height: 1em
}

#website-container a.headerlink:after {
  float: right;
  color: var(--headline-presudo);
  content: '\f0c1';
  font-size: .95em;
  transition: all .3s
}

#website-container a.headerlink:hover:after {
  color: var(--pseudo-hover)
}

#website-container a.headerlink:after {
  opacity: 0
}

#website-container h1:hover a.headerlink:after,
#website-container h2:hover a.headerlink:after,
#website-container h3:hover a.headerlink:after,
#website-container h4:hover a.headerlink:after,
#website-container h5:hover a.headerlink:after,
#website-container h6:hover a.headerlink:after {
  opacity: 1
}

#website-container ol ol,
#website-container ol ul,
#website-container ul ol,
#website-container ul ul {
  padding-left: 20px
}

#website-container ol li,
#website-container ul li {
  margin: 4px 0
}

#website-container ol p,
#website-container ul p {
  margin: 0 0 8px
}

#website-container>:last-child {
  margin-bottom: 0 !important
}

#website-container .flink {
  margin-bottom: 20px
}

#website-container .flink .flink-list {
  overflow: auto;
  padding: 10px 10px 0;
  text-align: center
}

#website-container .flink .flink-list>.flink-list-item {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 15px 7px;
  width: calc(100% / 4 - 15px);
  height: 90px;
  border-radius: 8px;
  line-height: 17px;
  -webkit-transform: translateZ(0)
}

@media screen and (max-width:1024px) {
  #website-container .flink .flink-list>.flink-list-item {
      width: calc(50% - 15px) !important
  }
}

@media screen and (max-width:600px) {
  #website-container .flink .flink-list>.flink-list-item {
      width: calc(100% - 15px) !important
  }
}

#website-container .flink .flink-list>.flink-list-item:hover .flink-item-icon {
  margin-left: -10px;
  width: 0
}

#website-container .flink .flink-list>.flink-list-item:before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: var(--text-bg-hover);
  content: '';
  transition: transform .3s ease-out;
  transform: scale(0)
}

#website-container .flink .flink-list>.flink-list-item:active:before,
#website-container .flink .flink-list>.flink-list-item:focus:before,
#website-container .flink .flink-list>.flink-list-item:hover:before {
  transform: scale(1)
}

#website-container .flink .flink-list>.flink-list-item a {
  color: var(--font-color);
  text-decoration: none
}

#website-container .flink .flink-list>.flink-list-item a .flink-item-icon {
  float: left;
  overflow: hidden;
  margin: 15px 10px;
  width: 60px;
  height: 60px;
  border-radius: 35px;
  transition: width .3s ease-out
}

#website-container .flink .flink-list>.flink-list-item a .flink-item-icon img {
  width: 100%;
  height: 100%;
  transition: filter 375ms ease-in .2s, transform .3s;
  object-fit: cover
}

#website-container .flink .flink-list>.flink-list-item a .img-alt {
  display: none
}

#website-container .flink .flink-item-name {
  padding: 16px 10px 0 0;
  height: 40px;
  font-weight: 700;
  font-size: 1.2em
}

#website-container .flink .flink-item-desc {
  padding: 0;
  height: 50px;
  font-size: .93em
}

#website-container .flink .flink-name {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 1.5em
}
/* website-2 样式 -------------------- 结束 -------------------- */

